import { actions } from "./store"

const defaultStore = {
    notes : []
}

export let notesReducer = (state = defaultStore, action) =>{
        switch (action.type){
            
            case actions.deleteNote:
                return{...state, notes: state.notes.filter(note => note.id !== action.payload)}
            case actions.addNote:
                return {...state, notes: [...state.notes, action.payload]}
        
            default:
                return state
        
}
}

export const deleteNote = (dispatch, payload) =>{
    dispatch({type:actions.deleteNote, payload: payload})
}

export const addNote = (dispatch, payload) =>{
    dispatch({type:actions.addNote, payload: payload})
}

        

        